.header{
    flex:1;
    background-color: var(--bg-color-1);
    padding: 20px;
    overflow-y: scroll;
}
.header p{
    font-size: 1rem;
    line-height: 1.85rem;
    color: var(--text-color-1); 
    display: flex;
    justify-content: flex-end;

}
.header_expression{
    overflow-x: scroll;
    width: 100%;
    min-height: 30px;;
}
.header_expression p{
    white-space: nowrap;
    min-width:100%;
    width: fit-content ;
    font-weight: bold;
}
.header_result{
    font-weight: bold;
    font-size: 1.3rem !important;
}